import BooksContainer from "../components/booksContainer";
import { Book } from "@/types";
import { GET_BOOKS } from "@/backend/route";


export default function Home() {
    const booksList = GET_BOOKS

    const formatBooks: Book[] = booksList.map((book) => ({
        Title: book.Title,
        Author: book.Author,
        ISBN: book.ISBN,
        PublicationYear: book.PublicationYear
    }))


    return (
        <div>
            <h1>search test</h1>
            <BooksContainer />
        </div>      
    );
  }
  